* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Microsoft YaHei', sans-serif;
}

#root, .app, #map-container {
  width: 100%;
  height: 100%;
}

.university-detail {
  width: 100%;
  position: fixed;
  bottom: 0px;
  z-index: 233;
  background: #fff;
  padding: 10px 20px;
  -webkit-box-shadow: 1rem 1rem 0.1rem rgba(0,0,0,.2), -0.06rem 0 0.06rem rgba(0,0,0,.1);
          box-shadow: 1rem 1rem 0.1rem rgba(0,0,0,.2), -0.06rem 0 0.06rem rgba(0,0,0,.1);
  -webkit-transition: .3s all ease-in-out;
  -o-transition: .3s all ease-in-out;
  transition: .3s all ease-in-out;
}

.university-detail.invisible {
  bottom: -150px;
}

.university-detail.visible {
  bottom: 0px;
}

.university-name {
  margin: 0px;
  margin-right: 20px;
  display: inline-block;
  font-size: 24px;
}

.university-address {
  font-size: 14px;
  margin: 4px 0px;
  color: #888;
}

.university-members {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.person {
  width: 50%;
  padding: 0px 20px 0px 0px;
  margin: 8px 0px;
}

.one-person .person {
  width: 100%;
}

.major {
  padding-left: 5px;
}

.go-there {
  width: 60px;
  height: 60px;
  position: absolute;
  right: 10px;
  top: -30px;
  background: rgba(0, 122, 204);
  color: #fff;
  font-size: 24px;
  text-align: center;
  line-height: 60px;
  border-radius: 50%;
  cursor: pointer;
}

.search-container {
  width: 100%;
  padding: 20px 10px;
  position: absolute;
  top: 0px;
}

.search-container  input {
  width: 100%;
  border: 0px;
  font-size: 18px;
  width: 100%;
  outline: 0;
  padding: 10px 10px;
  -webkit-box-shadow: 0.1rem 0.1rem 0.1rem rgba(0,0,0,.2), -0.06rem 0 0.06rem rgba(0,0,0,.1);
          box-shadow: 0.1rem 0.1rem 0.1rem rgba(0,0,0,.2), -0.06rem 0 0.06rem rgba(0,0,0,.1);
}

.result-container {
  width: 100%;
  margin-top: 20px;
  background: #fff;
  max-height: 400px;
  overflow-y: auto;
  -webkit-box-shadow: 0.1rem 0.1rem 0.1rem rgba(0,0,0,.2), -0.06rem 0 0.06rem rgba(0,0,0,.1);
          box-shadow: 0.1rem 0.1rem 0.1rem rgba(0,0,0,.2), -0.06rem 0 0.06rem rgba(0,0,0,.1);
}

.result-item {
  width: 100%;
  padding: 10px;
}

.result-item-right {
  font-size: 14px;
  display: block;
  color: #888;
}
.result-item-university {
  margin-right: 10px;
}

@media screen and (min-width: 600px) {
  .result-item-right {
    float: right;
  }
}


